/**
* Media queries
**/
.product-slider-section {
  padding: 0 0 200px;
}
.product-slider-section .product-tabs {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}
@media only screen and (min-width: 992px) {
  .product-slider-section .product-tabs {
    justify-content: flex-start;
  }
}
.product-slider-section .product-tabs__title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-1);
  color: var(--clr-smoke-2);
  line-height: 44px;
  letter-spacing: normal;
  text-transform: none;
  opacity: 0.5;
  margin: 0;
}
.product-slider-section .product-tabs__title--active {
  color: var(--clr-black);
  opacity: 1;
}
@media only screen and (min-width: 400px) {
  .product-slider-section .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2);
  }
}
@media only screen and (min-width: 992px) {
  .product-slider-section .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2-1);
    line-height: 44px;
  }
}
.product-slider-section .product-tabs__separator {
  margin: 0 10px;
  width: 1px;
  background-color: var(--clr-smoke-2);
  opacity: 0.5;
}
.product-slider-section .product-tabs__separator--vertical {
  height: 40px;
  width: 1px;
}
.product-slider-section .product-tabs__separator--horizontal {
  height: 1px;
  width: 100%;
  margin: 30px 0 61px;
}
.product-slider-section .swiper {
  width: 100%;
  height: 100%;
  max-height: 471px;
}
.product-slider-section .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.product-slider-section .swiper-slide img {
  display: block;
  width: 100%;
  height: auto;
}
.product-slider-section .swiper-controller {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product-slider-section .swiper-button-next,
.product-slider-section .swiper-button-prev {
  width: 66px;
  height: 66px;
  aspect-ratio: 1;
  background-color: #d9d9d9;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product-slider-section .swiper-button-next svg,
.product-slider-section .swiper-button-prev svg {
  max-width: 25px;
}
.product-slider-section .swiper-button-next {
  left: 100%;
  margin-left: 10px;
}
.product-slider-section .swiper-button-prev {
  right: 100%;
  left: auto;
  margin-right: 10px;
}
.product-slider-section .swiper-button-next:after,
.product-slider-section .swiper-button-prev:after {
  display: none;
}
.product-slider-section .swiper-button-prev svg {
  transform: rotate(180deg);
}